<template>
  <div>
    <div class="fasilitas-form">
      <h1 class="content-header" @click="generateData">
        {{ this.$route.name }}
      </h1>
      <base-header class="pt-4 pb-8" type="primary"></base-header>
      <div class="container-fluid mt--8">
        <div class="row card-wrapper">
          <div class="form-info col-12">
            <div class="card">
              <div class="card-header">
                <h2 class="mb-0">Form Fasilitas</h2>
              </div>
              <div class="card-body">
                <form>
                  <base-input label="Cabang/Unit Kerja">
                    <el-select
                      v-model="form.unit"
                      size="mini"
                      placeholder="Cabang/Unit Kerja"
                      :disabled="isUbah"
                    >
                      <el-option
                        v-for="option in cabangShift(cabang)"
                        :key="option.label"
                        :label="option.label"
                        :value="option.value"
                      ></el-option>
                    </el-select>
                  </base-input>
                  <base-input label="Jenis Fasilitas">
                    <el-select
                      v-model="form.type"
                      size="mini"
                      placeholder="Fasilitas"
                    >
                      <el-option
                        v-for="option in jenis"
                        :key="option.label"
                        :label="option.label"
                        :value="option.value"
                      ></el-option>
                    </el-select>
                  </base-input>
                  <base-input label="Nama Fasilitas">
                    <textarea
                      v-model="form.name"
                      class="form-control"
                      id="nama-fasilitas"
                      rows="1"
                      resize="none"
                    ></textarea>
                  </base-input>
                  <base-input label="Deskripsi Singkat">
                    <textarea
                      v-model="form.overview"
                      class="form-control"
                      id="deskripsi-singkat"
                      rows="2"
                      resize="none"
                    ></textarea>
                  </base-input>
                  <base-input label="Konten">
                    <html-editor v-model="form.content.text"></html-editor>
                  </base-input>
                  <base-input label="Cover">
                    <div v-if="files.cover[0].upload == 0" class="custom-file">
                      <input
                        id="input-file-cover"
                        type="file"
                        ref="cover"
                        class="custom-file-input"
                        @change="fileHandler('cover')"
                        @focus="resetInput('input-file-cover')"
                      />
                      <label class="custom-file-label" for="customFileLang"
                        >Pilih gambar</label
                      >
                    </div>
                    <div v-if="files.cover[0].upload > 0" class="row">
                      <div class="col-6">
                        <label>{{ files.cover[0].name }}</label>
                      </div>
                      <div class="col-6 text-right">
                        <base-button
                          v-if="files.cover[0].upload == 2"
                          size="sm"
                          type="danger"
                          @click="deleteFile('cover', 0)"
                          >Hapus</base-button
                        >
                      </div>
                    </div>
                    <base-progress
                      v-if="files.cover[0].upload == 1"
                      class="mb-0"
                      :type="
                        files.cover[0].progress >= 100 ? 'green' : 'orange'
                      "
                      :value="files.cover[0].progress"
                    ></base-progress>
                    <img
                      v-if="files.cover[0].upload == 2"
                      class="w-100"
                      :src="files.cover[0].path"
                    />
                  </base-input>
                  <base-input label="Galeri Gambar">
                    <div class="custom-file">
                      <input
                        id="input-file-galeri"
                        type="file"
                        ref="galeri"
                        multiple
                        class="custom-file-input"
                        @change="fileHandler('galeri')"
                        @focus="resetInput('input-file-galeri')"
                        :disabled="isUploadingGaleri()"
                      />
                      <label class="custom-file-label" for="customFileLang"
                        >Pilih galeri gambar</label
                      >
                    </div>
                    <template v-for="(item, index) in files.galeri">
                      <div
                        :key="'header: (' + index + ')' + item.name"
                        v-if="
                          files.galeri.length > 0 &&
                          files.galeri[index].upload > 0
                        "
                        class="row mt-4"
                      >
                        <div class="col-6">
                          <label>{{ files.galeri[index].name }}</label>
                        </div>
                        <div class="col-6 text-right">
                          <base-button
                            v-if="
                              !isUploadingGaleri() &&
                              files.galeri[index].upload == 2
                            "
                            size="sm"
                            type="danger"
                            @click="deleteFile('galeri', index)"
                            >Hapus</base-button
                          >
                        </div>
                      </div>
                      <base-progress
                        v-if="
                          files.galeri.length > 0 &&
                          files.galeri[index].upload == 1
                        "
                        :key="'loading: (' + index + ')' + item.name"
                        class="mb-0"
                        :type="item.progress >= 100 ? 'green' : 'orange'"
                        :value="item.progress"
                      ></base-progress>
                      <img
                        v-if="
                          files.galeri.length > 0 &&
                          files.galeri[index].upload == 2
                        "
                        :key="'image: (' + index + ')' + item.name"
                        class="w-100"
                        :src="item.path"
                      />
                    </template>
                  </base-input>
                  <div class="row">
                    <div class="col text-right">
                      <base-button type="danger" @click="goBack"
                        >Kembali</base-button
                      >
                      <base-button type="primary" @click="submitForm"
                        >Simpan</base-button
                      >
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Table, TableColumn, Select, Option } from "element-ui";
import HtmlEditor from "@/components/Inputs/HtmlEditor";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import Cabang from "../cabang";
import Jenis from "./jenis";
import Axios from "axios";
import Swal from "sweetalert2";
import "sweetalert2/dist/sweetalert2.css";

const intRNG = (min, max) => {
  return Math.floor(Math.random() * (max - min) + min);
};
var jsonQuery = require("json-query");

export default {
  components: {
    HtmlEditor,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
  },
  data() {
    return {
      api: this.$store.getters.getAPI,
      cabang: Cabang,
      files: {
        cover: [
          {
            file: null,
            name: "",
            path: "",
            progress: 0,
            upload: 0,
          },
        ],
        galeri: [
          {
            file: null,
            name: "",
            path: "",
            progress: 0,
            upload: 0,
          },
        ],
      },
      form: {
        content: {
          images: null,
          text: "",
        },
        cover: null,
        name: null,
        overview: null,
        type: null,
        unit: null,
      },
      jenis: Jenis,
    };
  },
  computed: {
    isUbah() {
      if (this.$route.params.method === "ubah") {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    getUnitName(id) {
      for (let index = 0; index < this.cabang.length; index++) {
        if (this.cabang[index].value === id) {
          return this.cabang[index].label;
        }
      }
    },
    goBack() {
      this.$router.push("/fasilitas/daftar");
    },
    generateData() {
      const dummy = {
        word: [
          "Lorem",
          "Ipsum",
          "Dolor",
          "Sit",
          "Amet",
          "Consectetur",
          "Adipiscing",
          "Elit",
          "Sed",
          "Do",
          "Eismod",
          "Tempor",
          "Incididunt",
          "Ut",
          "Labore",
          "Et",
          "Dolore",
          "Magna",
          "Aliqua",
        ],
        sentence: [
          "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
          "Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.",
          "Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.",
          "Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.",
        ],
        image: [
          {
            file: null,
            name: "HoustonBayportred.png",
            path:
              "https://www.joc.com/sites/default/files/field_feature_image/HoustonBayportred.png",
            progress: 100,
            upload: 2,
          },
          {
            file: null,
            name: "120819_Port-Liner-Connectivity-Index.jpg",
            path:
              "https://atlas-network.com/wp-content/uploads/2019/08/120819_Port-Liner-Connectivity-Index.jpg",
            progress: 100,
            upload: 2,
          },
          {
            file: null,
            name: "images_1580969178013_154474_portofsinesportugal_480844.jpg",
            path:
              "https://www.mumbailive.com/images/media/images/images_1580969178013_154474_portofsinesportugal_480844.jpg?bg=7eb9e9&crop=480%2C269.4736842105263%2C0%2Cnull&fit=crop&fitToScale=w%2C1368%2C768&h=768&height=288&w=1368&width=480",
            progress: 100,
            upload: 2,
          },
          {
            file: null,
            name: "Top-15-Ports-Updated-1200.png",
            path:
              "https://nowthatslogistics.com/wp-content/uploads/2019/06/Top-15-Ports-Updated-1200.png",
            progress: 100,
            upload: 2,
          },
          {
            file: null,
            name:
              "Port-Miami-South-Florida-Container-Terminal-e1525939217132.jpg",
            path:
              "https://safety4sea.com/wp-content/uploads/2017/01/Port-Miami-South-Florida-Container-Terminal-e1525939217132.jpg",
            progress: 100,
            upload: 2,
          },
          {
            file: null,
            name: "11-19-strotterdam__01.jpg",
            path:
              "https://wi-images.condecdn.net/image/gQn6A81V0nj/crop/2040/f/11-19-strotterdam__01.jpg",
            progress: 100,
            upload: 2,
          },
          {
            file: null,
            name: "SIPG.jpg",
            path:
              "https://www.seatrade-maritime.com/sites/seatrade-maritime.com/files/styles/article_featured_retina/public/uploads/2019/09/SIPG.jpg?itok=fLruJhUX",
            progress: 100,
            upload: 2,
          },
          {
            file: null,
            name: "Guangzhou port.jpg",
            path:
              "https://www.seatrade-maritime.com/sites/seatrade-maritime.com/files/styles/article_featured_retina/public/Guangzhou%20port.jpg?itok=6o2Qeeo2",
            progress: 100,
            upload: 2,
          },
          {
            file: null,
            name: "5d413b1367b0a90cd8688ea1.jpg",
            path:
              "https://i.hurimg.com/i/hdn/75/0x0/5d413b1367b0a90cd8688ea1.jpg",
            progress: 100,
            upload: 2,
          },
          {
            file: null,
            name: "top-view-of-port.jpeg",
            path:
              "https://www.shell.com/business-customers/marine/port-network-and-services-guide/_jcr_content/pagePromo/image.img.960.jpeg/1543423061140/top-view-of-port.jpeg",
            progress: 100,
            upload: 2,
          },
        ],
        long: [
          '<p><strong>The standard Lorem Ipsum passage, used since the 1500s</strong></p><p>"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."</p>',
          '<p><strong>The standard Lorem Ipsum passage, used since the 1500s</strong></p><p>"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."</p><p><br></p><p><strong>Section 1.10.32 of "de Finibus Bonorum et Malorum", written by Cicero in 45 BC</strong></p><p>"Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo. Nemo enim ipsam voluptatem quia voluptas sit aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione voluptatem sequi nesciunt. Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit, sed quia non numquam eius modi tempora incidunt ut labore et dolore magnam aliquam quaerat voluptatem. Ut enim ad minima veniam, quis nostrum exercitationem ullam corporis suscipit laboriosam, nisi ut aliquid ex ea commodi consequatur? Quis autem vel eum iure reprehenderit qui in ea voluptate velit esse quam nihil molestiae consequatur, vel illum qui dolorem eum fugiat quo voluptas nulla pariatur?"</p><p><br></p><p><strong>1914 translation by H. Rackham</strong></p><p>"But I must explain to you how all this mistaken idea of denouncing pleasure and praising pain was born and I will give you a complete account of the system, and expound the actual teachings of the great explorer of the truth, the master-builder of human happiness. No one rejects, dislikes, or avoids pleasure itself, because it is pleasure, but because those who do not know how to pursue pleasure rationally encounter consequences that are extremely painful. Nor again is there anyone who loves or pursues or desires to obtain pain of itself, because it is pain, but because occasionally circumstances occur in which toil and pain can procure him some great pleasure. To take a trivial example, which of us ever undertakes laborious physical exercise, except to obtain some advantage from it? But who has any right to find fault with a man who chooses to enjoy a pleasure that has no annoying consequences, or one who avoids a pain that produces no resultant pleasure?"</p><p><br></p><p><strong>Section 1.10.33 of "de Finibus Bonorum et Malorum", written by Cicero in 45 BC</strong></p><p>"At vero eos et accusamus et iusto odio dignissimos ducimus qui blanditiis praesentium voluptatum deleniti atque corrupti quos dolores et quas molestias excepturi sint occaecati cupiditate non provident, similique sunt in culpa qui officia deserunt mollitia animi, id est laborum et dolorum fuga. Et harum quidem rerum facilis est et expedita distinctio. Nam libero tempore, cum soluta nobis est eligendi optio cumque nihil impedit quo minus id quod maxime placeat facere possimus, omnis voluptas assumenda est, omnis dolor repellendus. Temporibus autem quibusdam et aut officiis debitis aut rerum necessitatibus saepe eveniet ut et voluptates repudiandae sint et molestiae non recusandae. Itaque earum rerum hic tenetur a sapiente delectus, ut aut reiciendis voluptatibus maiores alias consequatur aut perferendis doloribus asperiores repellat."</p><p><br></p><p><strong>1914 translation by H. Rackham</strong></p><p>"On the other hand, we denounce with righteous indignation and dislike men who are so beguiled and demoralized by the charms of pleasure of the moment, so blinded by desire, that they cannot foresee the pain and trouble that are bound to ensue; and equal blame belongs to those who fail in their duty through weakness of will, which is the same as saying through shrinking from toil and pain. These cases are perfectly simple and easy to distinguish. In a free hour, when our power of choice is untrammelled and when nothing prevents our being able to do what we like best, every pleasure is to be welcomed and every pain avoided. But in certain circumstances and owing to the claims of duty or the obligations of business it will frequently occur that pleasures have to be repudiated and annoyances accepted. The wise man therefore always holds in these matters to this principle of selection: he rejects pleasures to secure other greater pleasures, or else he endures pains to avoid worse pains."</p><p><br></p>',
        ],
      };
      const generateGallery = (dummy, count) => {
        let array = [];
        for (let index = 0; index < count; index++) {
          array.push(dummy[intRNG(0, dummy.length - 1)]);
        }
        return array;
      };
      this.files = {
        cover: [dummy.image[intRNG(0, dummy.image.length - 1)]],
        galeri: generateGallery(dummy.image, dummy.image.length - 1),
      };
      let unit = this.cabang[intRNG(1, this.cabang.length - 1)].label;
      if (this.$route.params.method === "tambah") {
        this.form.unit = unit;
      } else {
        this.form.unit = this.$store.getters.getUbahFasilitas.unit;
      }
      let data = {
        cabang: this.cabang,
      };
      this.form = {
        content: {
          images: this.files.galeri,
          text: dummy.long[0],
        },
        cover: this.files.cover,
        // name:
        //   dummy.word[intRNG(0, dummy.word.length - 1)] +
        //   " " +
        //   dummy.word[intRNG(0, dummy.word.length - 1)],
        name: "Pelabuhan " + this.getUnitName(this.form.unit),
        overview: dummy.sentence[intRNG(0, dummy.sentence.length - 1)],
        type: this.jenis[intRNG(0, this.jenis.length - 1)].value,
      };
    },
    submitForm() {
      this.form.cover = this.files.cover;
      this.form.content.images = this.files.galeri;
      this.form.timestamp = new Date().getTime();
      Swal.fire({
        title: `Konfirmasi`,
        text: "Apakah anda yakin untuk menyimpan data?",
        showCancelButton: true,
        icon: "question",
      }).then((result) => {
        if (result.isConfirmed) {
          if (this.$route.params.method === "ubah") {
            if (this.$store.getters.isDebugging) {
              console.log("DEBUG: Initiating PUT. Data: ", this.form);
            }
            Axios.put(
              this.$store.getters.getAPI.url +
                this.$store.getters.getAPI.fs.putFS +
                this.$store.getters.getUbahFasilitas.id,
              this.form
            )
              .then((response) => {
                Swal.fire("Data tersimpan", "", "success").then(() => {
                  this.$router.push("/fasilitas/daftar");
                });
              })
              .catch((error) => {
                Swal.fire("Data tidak tersimpan", "ERROR: " + error, "error");
              });
          } else {
            if (this.$store.getters.isDebugging) {
              console.log("DEBUG: Initiating POST. Data: ", this.form);
            }
            Axios.post(
              this.$store.getters.getAPI.url +
                this.$store.getters.getAPI.fs.postFS,
              this.form
            )
              .then((response) => {
                Swal.fire("Data tersimpan", "", "success").then(() => {
                  this.$router.push("/fasilitas/daftar");
                });
              })
              .catch((error) => {
                Swal.fire("Data tidak tersimpan", "ERROR: " + error, "error");
              });
          }
        } else {
          Swal.fire("Data tidak tersimpan", "", "error");
        }
      });
    },
    getType() {
      Axios.get(
        this.$store.getters.getAPI.url + this.$store.getters.getAPI.fs.getType
      )
        .then((response) => {
          if (this.$store.getters.isDebugging) {
            console.log(
              "DEBUG: Success to get rating list. Data: ",
              response.data
            );
          }
          let array = [];
          for (let i = 0; i < response.data.length; i++) {
            array.push({
              value: response.data[i].ID,
              label: response.data[i].NAME,
            });
          }
          array.sort((a, b) => {
            let aVal = a.label;
            let bVal = b.label;
            return bVal - aVal;
          });
          this.jenis = array;
          // this.renders.loading = false;
        })
        .catch((error) => {
          if (this.$store.getters.isDebugging) {
            console.log("ERROR: Failed to get rating list. ", error);
          }
        });
    },
    resetInput(id) {
      document.getElementById(id).value = "";
    },
    isUploadingGaleri() {
      let array = [];
      let sum = 0;
      for (let index = 0; index < this.files.galeri.length; index++) {
        if (this.files.galeri[index].upload == 1) {
          array.push(1);
        } else {
          array.push(0);
        }
      }
      array.forEach((item) => {
        sum += item;
      });
      if (sum > 0) {
        return true;
      } else {
        return false;
      }
    },
    deleteFile(ref, index) {
      if (index > -1) {
        this.files[ref].splice(index, 1);
      }
      if (this.files[ref].length == 0) {
        this.files[ref].push({
          file: null,
          name: "",
          path: "",
          progress: 0,
          upload: 0,
        });
      }
    },
    fileHandler(ref) {
      let files = this.$refs[ref].files;
      switch (ref) {
        case "cover":
          // console.log("single");
          this.files.cover[0].file = files[0];
          this.files.cover[0].name = files[0].name;
          this.files.cover[0].progress = 0;
          this.fileUpload("cover", 0);
          break;

        case "galeri":
          // console.log("multiple");
          let delta = 0;
          if (this.files.galeri[this.files.galeri.length - 1].upload == 2) {
            delta = this.files.galeri.length;
          } else {
            this.files.galeri = [];
          }
          for (let index = 0; index < files.length; index++) {
            this.files.galeri.push({
              file: files[index],
              name: files[index].name,
              path: "",
              progress: 0,
              upload: 0,
            });
            this.fileUpload("galeri", index + delta);
          }
          break;

        default:
          break;
      }
    },
    fileUpload(ref, index) {
      this.files[ref][index].upload = 1;
      let formData = new FormData();
      formData.append("picture", this.files[ref][index].file);
      Axios.post("http://45.127.133.75:3000/api/iems/img", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
        onUploadProgress: function (progressEvent) {
          this.files[ref][index].progress = parseInt(
            Math.round((progressEvent.loaded / progressEvent.total) * 100)
          );
        }.bind(this),
      })
        .then((res) => {
          this.files[ref][index].upload = 2;
          this.files[ref][index].path =
            "http://45.127.133.75:3000" + res.data.path;
        })
        .catch((error) => {
          this.files[ref][index].upload = 0;
          this.files[ref][index].progress = 0;
          Swal.fire("Gambar tidak tersimpan", "ERROR: " + error, "error");
        });
    },
    cabangShift(data) {
      let array = [];
      for (let i = 1; i < data.length; i++) {
        array.push(data[i]);
      }
      return array;
    },
  },
  created() {
    this.getType();
    if (this.$route.params.method === "ubah") {
      if (this.$store.getters.getUbahFasilitas) {
        this.form = this.$store.getters.getUbahFasilitas;
        this.files = {
          cover: this.form.cover,
          galeri: this.form.content.images,
        };
      } else {
        this.$router.push("tambah");
      }
    }
  },
};
</script>

<style lang="scss">
.fasilitas-form {
  .dz-message {
    .dz-button {
      background: hsla(0, 0%, 100%, 0);
      border: none;
      color: #8898aa;
    }
  }
}
</style>
